@use "global" as *;
.normal-block{
  padding: rem(40) 0 0;
}
.normal-block__title {
  position: relative;
  margin-top: rem(40);
  margin-bottom: rem(40);
  font-size: rem(24);
  font-weight: normal;
  border-bottom: 2px solid #374C69;
  text-align: center;
  font-family: $base-font-family;
  @include mq(md) {
    font-size: rem(32);
  }
}
.normal-block__text {
}
.normal-block__container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  @include mq(md) {
    flex-direction: row;
  }
  .normal-block__column {
    margin: rem(10);
    width: 100%;
    @include mq(md) {
      width: calc((100% - rem(40)) / 2);
    }
  }
}

.normal-block__column {
  img {
    display: inline-block;
  }
  p {
    margin-top: 0;
  }

}

.child-page {
  font-family: $second-font-family;
  h4 {
    margin-top: rem(20);
    margin-bottom: rem(20);
    padding: rem(10) rem(20);
    font-size: rem(18);
    color: #fff;
    background-color: #374C69;
    border-left: 5px solid #4C78B5;
    @include mq(md) {
      font-size: rem(22);
    }
  }
  p {
    font-size: rem(14);
    @include mq(md) {
      font-size: rem(16);
    }
  }
  ul {
    padding-left: rem(20);
    li {
      margin-top: rem(10);
      list-style: square;
      font-size: rem(14);
      @include mq(md) {
        font-size: rem(16);        
      }
      &::marker {
        color: #4C78B5;
      }
    }
  }
  .background {
    margin: 20px 0;
    padding: rem(20) rem(20);
  }
  .gray {
    background-color: #f8f9fa !important;
  }
}